import axios from "axios"

// Estender o tipo AxiosInstance para incluir nossa função personalizada
declare module "axios" {
  interface AxiosInstance {
    verificarToken: () => boolean
  }
}

// URL base com o contexto correto
const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || "http://localhost:8080/sistema-gestao"
console.log("API Base URL:", API_BASE_URL)

const apiClient = axios.create({
  baseURL: API_BASE_URL,
  headers: {
    "Content-Type": "application/json",
  },
})

// Add request interceptor for authentication
apiClient.interceptors.request.use(
  (config) => {
    console.log(`Enviando requisição para: ${config.method?.toUpperCase()} ${config.url}`)

    // Add authentication token if available
    const token = localStorage.getItem("token")

    if (token) {
      console.log("Token encontrado:", token.substring(0, 10) + "...")

      // Garantir que o token esteja no formato correto
      config.headers.Authorization = `Bearer ${token.trim()}`

      // Log completo dos cabeçalhos para depuração
      console.log("Cabeçalhos completos:", JSON.stringify(config.headers, null, 2))
    } else {
      console.warn("Token não encontrado!")
    }

    return config
  },
  (error) => {
    console.error("Erro no interceptor de requisição:", error)
    return Promise.reject(error)
  },
)

// Add response interceptor for error handling
apiClient.interceptors.response.use(
  (response) => {
    console.log(`Resposta recebida: ${response.status} ${response.statusText}`)
    return response
  },
  (error) => {
    console.error("Erro na resposta:", error.message)

    if (error.response) {
      console.error(`Status: ${error.response.status}`)
      console.error(`Dados: ${JSON.stringify(error.response.data)}`)

      // Log adicional para erros 401 e 403
      if (error.response.status === 401) {
        console.error("Erro 401: Token inválido ou expirado")
        console.error("Cabeçalhos enviados:", JSON.stringify(error.config.headers, null, 2))
      } else if (error.response.status === 403) {
        console.error("Erro 403: Permissão negada")
        console.error("Cabeçalhos enviados:", JSON.stringify(error.config.headers, null, 2))
      }
    }

    return Promise.reject(error)
  },
)

// Adicionar função para verificar token (para compatibilidade)
apiClient.verificarToken = () => {
  const token = localStorage.getItem("token")
  if (!token) {
    console.warn("Token não encontrado no localStorage")
    return false
  }
  return true
}

export default apiClient
